import React from 'react'
import {bulletsColorsArr} from '../data/BulletsColorsCode'
import '../styles/scss/loader.scss'
const Loader = () => {
    return (
        <React.Fragment>
            <ul className='loader'>
            {bulletsColorsArr.map(({code,id},index)=>{
                return <>
                    <li   key={id}  style={{backgroundColor : code }}></li>
                </>
            })}
            </ul>
        </React.Fragment>
    )}
export default Loader
